@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    font: 400 14px Roboto, sans-serif;
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input{
    width: 100%;
    height: 40px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 20px;
}

form textarea{
    width: 100%;
    min-height: 140px;
    resize: vertical ;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 16px 20px;
    line-height: 24px;
}

.button {
    width: 100%;
    height: 40px;
    background: #e02041;
    border: 0;
    border-radius: 8px;
    color: #fff;
    font-weight: 700;
    margin-top: 10px;
    display: inline-block;
    text-decoration: none;
    font-size: 18px;
    line-height: auto;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
    /* margin-bottom: 10px; */
    text-align: center;

}

.button:hover {
    -webkit-filter: brightness(95%);
            filter: brightness(95%);
}

.back-link{
    display: flex;
    align-items: center;
    margin-top: 30px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}

.back-link svg{
    margin-right: 8px;
}

.back-link:hover{
    opacity: 0.5;
}
