 .login-container{
     width: 100%;
     max-width: 1120px;
     height: 100vh;
     margin: 0 auto;
     display: flex;
     align-items: center;
     justify-content: space-between;
 } 

 .login-container section.form{
    width: 100%;
    max-width: 350px;
    margin-right: 30px;
 }

 .login-container section.form form{
     margin-top: 100px;
 }

 .login-container section.form form input:first-of-type{
    margin-bottom: 10px;
    
}


 .login-container section.form form h1{
     font-size: 32px;
     margin-bottom: 32px;
 }


.cadastro-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cadastro-container .content {
    width: 100%;
    padding: 96px;
    background: #f0f0f5;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cadastro-cotainer .content  section {
    width: 100%;
    max-width: 380px;
}

.cadastro-container .content section h1 {
    margin: 64px 0 32px;
    font-size: 32px;
}

.cadastro-container .content section p {
    font-size: 18px;
    color: #737380;
    line-height: 25px;
}

.cadastro-container .content form {
    width: 100%;
    max-width: 450px;
}
.cadastro-container .content form input{
    margin-top: 8px;
}

.cadastro-container .content form .input-group {
    display: flex;
}
.cadastro-container .content form .input-group input + input {
    margin-left: 8px;
    text-align: center;
}

.profile-container {
    width: 100%;
    max-width: 1180px;
    margin: 32px auto;
    padding: 0 30px;
}


.profile-container header {
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
}

.profile-container header span{
    font-size: 20px;
    margin-left: 20px;   
}


.profile-container header img {
    height: 60px;
}

.profile-container header a {
    width: 250px;
    margin-left: auto;
    margin-top: 0;
    padding-top: 8px;
}

.profile-container header button{
    height: 42px;
    width: 42px;
    margin-left: 10px;
    border-radius: 8px;
    border: 1px solid #dcdce6; 
    background: transparent;
    /* margin-left: 16px; */
    transition:  border-color 0.2s;
    
} 

.profile-container header button:hover {
    border-color: #999;
}

.profile-container input {
    margin-top: 70px;
    display: flex;
    border-radius: 8px;
    padding-left: 10px;
    width: 100%;
    height: 30px;
    border: 1px solid #999 ;
}
.profile-container h1 {
    margin-top: 50px;
    margin-bottom: 24px;
}

.profile-container ul {
    display: grid;
    grid-template-columns: 1fr 1fr; /* or repeat(2, 1fr) */
    grid-gap: 24px;
    gap: 24px;
    list-style: none;
}

.profile-container ul li{
    background: #fff;
    padding: 24px;
    border: 1px solid #999;
    border-radius: 8px;
    position: relative;
    display: flex;
    align-items:  flex-start;
    justify-content: space-between;
}

.profile-container ul li strong {
    display: block;
    margin-bottom: 16px;
    color: #41414d;
}

.profile-container ul li p + strong{
    margin-top: 32px;
   /*  color: gold; */
}

.profile-container ul li p  {
    color: #737380;
    line-height: 21px;
    font-size: 16px;
}

.profile-container ul li .btn{
    display: flex;
    justify-content: flex-end;
    

}


.profile-container ul li .btn button {
    border: 0;
    border-radius: 2px;  
    /* padding: 2px; */  
    background: transparent; 
}

.profile-container li .btn button:hover {
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
    background: gainsboro;
}

.profile-container li .btn button + button {
    margin-left: 10px;
}




